body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

html, #root{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.selected {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    /*box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);*/
}

.display-5 {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.2;  
}

.compensate-left {
  margin-left: -0.06em;
}

.overlay, .loader-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  animation-name: fade-in;
  animation-duration: 250ms;
}

.loader-overlay {
  background-color: rgba(0,0,0,0.98);
}

@keyframes fade-in {
  from {opacity: 0}
  to {opacity: 1}
}

.loader {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #555;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 750ms cubic-bezier(0.605, 0.160, 0.270, 0.905) infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.z-0 {
  z-index: 0!important;
}

.z-1 {
  z-index: 1!important;
}

.z-2 {
  z-index: 2!important;
}

.z-3 {
  z-index: 3!important;
}

.h-10 {
  height: 10%!important;  
}

.h-60p{
  height: 60px!important;
}

.h-90 {
  height: 90%!important;
}

.mb-7 {
  margin-bottom: 12rem!important;
}

.pt-30 {
  padding-top: 30px!important;
}

.bg-image {
  position: absolute;
  background-image: url("/Weather/ngari-china.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(35%);
  height: 100%;
  width: 100%;
}

.transparent {
  background-color: #ffffff69;
}

.dir-ltr {
  direction: rtl!important;
}

.list-group {
  z-index: 1;
  width: 100%;
  padding-right: 30px;
  overflow-y: scroll;
  max-height: 40vh;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.list-group::-webkit-scrollbar {
  display: none;
}

.list-group.small {
  width: 125%;
  padding-right: 0;
  max-height: 90vh;
}

.list-group-item {
  border-top-width: 1px!important;
  border-bottom-width: 1px!important;
}

.list-group-item:hover{
  cursor: pointer;
  border-color: #80bdff;
  outline: 0;
}

.form-control:focus {
  box-shadow: none;
}

.tiny {
  width: 200px;
  padding-right: 0px;
}

.previous-btn {
  transition: all .2s ease-in-out;
}

.previous-btn:hover {
  cursor: pointer;
  transform: scale(1.5);
}

.header {
  background-color: #313a46;
}

.homeButton {
  cursor: pointer;
}

.one-line {
  word-spacing: 1px;
}

.text-cold {
  color: #47aefc
}

.text-warm {
  color: #e67e7e
}